import MicIcon from '@mui/icons-material/Mic';
import SendIcon from '@mui/icons-material/Send';

import {
    Box,
    Card,
    IconButton,
    TextField,
    Typography
} from '@mui/material';
import { useState } from 'react';
import { User } from '../model';

export const Assistant = ({ user }: { user: User }) => {
    const [messages] = useState([
      { type: 'user', content: 'Hi! Can you give me an overview of my income for this year?' },
      { type: 'assistant', content: 'Certainly! Based on the information I have, your total income for this year is 120,000 CHF. This includes your salary of 110,000 CHF and investment income of 10,000 CHF. Would you like a breakdown of your deductions as well?' },
      { type: 'user', content: 'Yes, please. What deductions am I eligible for?' },
      { type: 'assistant', content: `Great question! You're eligible for several deductions. Your main deductions are:

      1. 3rd Pillar contribution: 6,883 CHF
      2. Commuting expenses: 2,500 CHF
      3. Professional development costs: 2,000 CHF
      4. Charitable donations: 1,000 CHF

      Your total deductions amount to 12,383 CHF. Is there anything specific you'd like to know more about?` },
      { type: 'user', content: 'I\'ve heard about a home office deduction. Am I eligible for that?' },
      { type: 'assistant', content: 'Yes, you are! I apologize for not mentioning it earlier. Given that you worked from home for 60% of the year due to the pandemic, you\'re eligible for a home office deduction. Based on your living space and the percentage of time worked from home, you can deduct an additional 1,800 CHF. Would you like me to add this to your deductions?' },
      { type: 'user', content: 'Absolutely! That\'s great news. Can you tell me how this affects my taxable income?' },
      { type: 'assistant', content: `Certainly! Let's recalculate:

      Total income: 120,000 CHF
      Total deductions (including home office): 14,183 CHF
      
      Your taxable income is now 105,817 CHF. This reduction in taxable income could potentially save you around 1,500 CHF in taxes, depending on your tax rate. Is there anything else you'd like to know about your tax situation?` },
      { type: 'user', content: 'That\'s really helpful. One last question - do I owe any taxes or am I getting a refund?' },
      { type: 'assistant', content: 'Based on your withholding taxes and your final tax calculation, you\'re actually entitled to a refund of approximately 2,200 CHF. This is mainly due to your deductions being higher than initially estimated. Would you like me to proceed with filing your tax return with all this information?' },
    ]);
  
    return (
      <Box sx={{ p: 2, bgcolor: 'background.default', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
        <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}>
          <Box component="span" sx={{ color: 'primary.main' }}>You</Box> Assistant
        </Typography>
        <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">
          What can I do for you?
        </Typography>
        <Box sx={{ flexGrow: 1, overflowY: 'auto', mb: 2 }}>
          {messages.map((message, index) => (
            <Box key={index} sx={{ display: 'flex', justifyContent: message.type === 'user' ? 'flex-end' : 'flex-start', mb: 2 }}>
              <Card sx={{ bgcolor: 'background.paper', p: 2, maxWidth: '80%' }}>
                <Typography variant="body1" color={message.type === 'user' ? 'text.secondary' : 'text.primary'}>
                  {message.content}
                </Typography>
              </Card>
            </Box>
          ))}
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'center', mt: 'auto', pb: { xs: 7, md: 2 } }}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Ask anything..."
            sx={{
              bgcolor: 'background.paper',
              '& .MuiOutlinedInput-root': {
                '& fieldset': { borderColor: '#E5E7EB' },
                '&:hover fieldset': { borderColor: '#E5E7EB' },
                '&.Mui-focused fieldset': { borderColor: 'primary.main' },
              },
            }}
          />
          <IconButton sx={{ ml: 1, bgcolor: 'primary.main', color: 'white', '&:hover': { bgcolor: 'primary.dark' } }}>
            <SendIcon />
          </IconButton>
          <IconButton sx={{ ml: 1, bgcolor: 'secondary.main', color: 'white', '&:hover': { bgcolor: 'secondary.dark' } }}>
            <MicIcon />
          </IconButton>
        </Box>
      </Box>
    );
  };