const TrendChart = ({ data, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'flex-end' }}>
    {data.map((value, index) => (
      <Box
        key={index}
        sx={{
          width: `${100 / data.length}%`,
          height: `${(value / Math.max(...data)) * 100}%`,
          bgcolor: 'action.selected',
          mx: 0.5,
          '&:last-child': {
            bgcolor: color,
          },
        }}
      />
    ))}
  </Box>
);

const ProgressChart = ({ progress, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center' }}>
    <Box sx={{ width: '100%', bgcolor: 'action.hover', height: 10, borderRadius: 5 }}>
      <Box
        sx={{
          width: `${progress}%`,
          bgcolor: color,
          height: '100%',
          borderRadius: 5,
          transition: 'width 0.5s ease-in-out',
        }}
      />
    </Box>
  </Box>
);

const ComparisonChart = ({ current, previous, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'flex-end' }}>
    <Box
      sx={{
        width: '45%',
        height: `${(previous / Math.max(current, previous)) * 100}%`,
        bgcolor: 'action.hover',
        mr: 1,
      }}
    />
    <Box
      sx={{
        width: '45%',
        height: `${(current / Math.max(current, previous)) * 100}%`,
        bgcolor: color,
      }}
    />
  </Box>
);