export interface TaxDeductible {
  name: string;
  category: string; // New field
  isApplicable: (taxpayer: UserDetails) => boolean;
  calculateState: (taxpayer: UserDetails) => number;
  calculateFederal: (taxpayer: UserDetails) => number;
  description: string;
  maxAmountState?: number;
  maxAmountFederal?: number;
  minAmount?: number;
  percentage?: number;
  requiredAttributes: (keyof UserDetails)[];
  calculationBasis: 'per person' | 'per household';
  notes?: string;
}

export interface UserDetails {
  // Personal Information
  maritalStatus?: 'single' | 'married';
  numberOfChildren?: number;
  childrenAges?: number[];

  // Employment Status
  isEmployed?: boolean;
  isSelfEmployed?: boolean;

  // Income 
  income?: number;
  netIncome?: number;
  spouseIncome?: number;
  secondaryEmploymentIncome?: number;

  // Transportation
  publicTransportCost?: number;
  bicycle?: boolean;
  carKm?: number;
  motorcycleKm?: number;

  // Work-related Expenses
  mealsOutsideHome?: number;
  trainingCosts?: number;
  mealsSubsidized?: boolean;

  // Family
  childSupportPayments?: number;
  pensionPayments?: number;
  alimonyPayments?: number;
  spouseCollaboration?: number;

  // Financial
  debtInterest?: number;
  securitiesIncome?: number;
  securitiesValue?: number;

  thirdPillarContributions?: number;
  hasSecondPillar?: boolean;
  donationsAmount?: number;
  politicalDonations?: number;
  liquidationGains?: number;
  qualifiedParticipations?: number;


  // Insurance and Health
  healthInsurancePremiums?: number;
  disabilityRelatedCosts?: number;

  // Family and Care
  childcareCosts?: number;
  supportedPersons?: number;

  // Property
  propertyIncome?: number;
  propertyMaintenanceCosts?: number;

  // Wealth Management
  wealthManagementCosts?: number;
}

export function areAllRequiredAttributesDefined(taxpayer: UserDetails, deductible: TaxDeductible): boolean {
  return deductible.requiredAttributes.every(attr => attr in taxpayer && taxpayer[attr] !== undefined);
}

export function calculateTaxDeductibles(taxpayer: UserDetails): { state: number, federal: number } {
  let totalStateAmount = 0;
  let totalFederalAmount = 0;

  for (const deductible of taxDeductibles) {
    if (areAllRequiredAttributesDefined(taxpayer, deductible) && deductible.isApplicable(taxpayer)) {
      const stateAmount = deductible.calculateState(taxpayer);
      const federalAmount = deductible.calculateFederal(taxpayer);

      // Apply maximum amounts if defined
      const finalStateAmount = deductible.maxAmountState !== undefined
        ? Math.min(stateAmount, deductible.maxAmountState)
        : stateAmount;
      const finalFederalAmount = deductible.maxAmountFederal !== undefined
        ? Math.min(federalAmount, deductible.maxAmountFederal)
        : federalAmount;

      totalStateAmount += finalStateAmount;
      totalFederalAmount += finalFederalAmount;
    }
  }

  return {
    state: totalStateAmount,
    federal: totalFederalAmount
  };
}


