/***
 * Abzug fr fremdbetreute Kinder [Ziffer 16.6]
Kinderdrittbetreuungskosten: Der Abzug betrgt hchstens CHF 10'100 pro Kind. Fr jedes
Kind, welches das 14. Altersjahr noch nicht vollendet hat, knnen die nachgewiesenen Kosten
(hchstens CHF 10'100) abgezogen werden. Das Kind muss mit der steuerpflichtigen Person,
die fr seinen Unterhalt sorgt, im gleichen Haushalt leben. Die Kosten mssen in direktem
Zusammenhang mit der Erwerbsttigkeit, Ausbildung oder Erwerbsunfhigkeit der steuer-
pflichtigen Person stehen.
Konkubinatspaare, die mit Kindern unter gemeinsamer elterlicher Sorge in einem gemeinsamen
Haushalt leben, knnen je die Hlfte des Kinderdrittbetreuungskostenabzugs geltend machen.

Der halbe Abzug betrgt je hchstens CHF 5'050 pro Kind; eine andere Aufteilung ist von den
Eltern nachzuweisen. Dasselbe gilt fr Eltern mit Kindern unter alternierender Obhut, sofern
das Kind unter gemeinsamer elterlicher Sorge steht.
 */

taxDeductibles.push({
  name: "Child Care Costs (Kinderdrittbetreuungskosten)",
  category: "Weitere Abzge",
  description: "Deductions for third-party child care costs for children under 14 years old.",
  isApplicable: (taxpayer) => 
    taxpayer.childcareCosts > 0 && 
    (taxpayer.childrenAges?.some(age => age < 14) ?? false),
  calculateState: (taxpayer) => {
    const eligibleChildren = taxpayer.childrenAges?.filter(age => age < 14).length ?? 0;
    const maxDeduction = eligibleChildren * 10100;
    
    if (taxpayer.maritalStatus === 'married' || taxpayer.maritalStatus === 'single') {
      return Math.min(taxpayer.childcareCosts, maxDeduction);
    } else {
      // For unmarried couples living together (Konkubinatspaare)
      return Math.min(taxpayer.childcareCosts, maxDeduction / 2);
    }
  },
  calculateFederal: (taxpayer) => {
    // Assuming the same calculation for federal as for state
    const eligibleChildren = taxpayer.childrenAges?.filter(age => age < 14).length ?? 0;
    const maxDeduction = eligibleChildren * 10100;
    
    if (taxpayer.maritalStatus === 'married' || taxpayer.maritalStatus === 'single') {
      return Math.min(taxpayer.childcareCosts, maxDeduction);
    } else {
      return Math.min(taxpayer.childcareCosts, maxDeduction / 2);
    }
  },
  requiredAttributes: ["childcareCosts", "childrenAges", "maritalStatus"],
  calculationBasis: 'per household',
  maxAmountState: 10100,
  maxAmountFederal: 10100,
  notes: "Deduction of up to CHF 10,100 per child under 14 years old. The child must live in the same household as the taxpayer. Costs must be directly related to the taxpayer's employment, education, or incapacity to work. Unmarried couples living together with children under joint parental care can each claim half of the deduction (max CHF 5,050 per child), unless they provide evidence for a different split. The same applies to parents with alternating custody under joint parental care."
});

