import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {
    Box,
    Button,
    Card,
    Collapse,
    Divider,
    Grid,
    Typography
} from '@mui/material';
import { useState } from 'react';
import { Taxable, TaxableType, User } from '../../model';
import { calculateTaxDeductibles } from '../../model-deductibles';

export const TaxesOverview = ({ user, taxables }: { user: User, taxables: Taxable[] }) => {
    const [showExtendedView, setShowExtendedView] = useState(false);

    const sumTaxable = (type: TaxableType) => {
        return taxables.filter(t => t.type === type).reduce((acc, t) => acc + t.amount, 0) || 0;
    }

    const getIncome = (): number => {
        return Math.floor(sumTaxable(TaxableType.INCOME))
    }

    const getFederalIncome = (): number => {
        return getIncome() - calculateTaxDeductibles(user.details).federal
    }

    const getStateIncome = (): number => {
        return getIncome() - calculateTaxDeductibles(user.details).state
    }

    const getWealth = (): number => {
        return Math.floor(sumTaxable(TaxableType.ASSET))
    }

    const getSimpleTax = (): number => {
        return getStateIncome() * user?.tarif.taxRateIncome + getWealth() * user?.tarif.taxRateWealth;
    }

    const getBundTax = (): number => {
        return getFederalIncome() * user?.tarif.taxRateBund;
    }

    const getKantonTax = (): number => {
        return getSimpleTax() * user?.tarif.taxRateKanton;
    }

    const getGemeindeTax = (): number => {
        return getSimpleTax() * user?.tarif.taxRateGemeinde;
    }

    const getTotalTax = (): number => {
        return getKantonTax() + getGemeindeTax() + getBundTax();
    }

    const taxesBreakdown = () => {
        return (
            <Box>
                <Typography variant="h6" gutterBottom sx={{ mb: 2 }}>Tax Calculation Breakdown</Typography>
                
                <Box sx={{ mb: 3 }}>
                    <Typography variant="subtitle2" gutterBottom sx={{ color: 'primary.main' }}>Federal Tax</Typography>
                    <Typography variant="caption" color="text.secondary" sx={{ mb: 1 }}>
                        Federal tax is calculated based on your federal taxable income, using progressive tax rates.
                    </Typography>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2">Federal Tax</Typography>
                        <Box sx={{ textAlign: 'right' }}>
                            <Typography variant="body2">{getBundTax().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                            <Typography variant="caption" color="text.secondary">{`${getFederalIncome().toLocaleString()}  ${(user.tarif.taxRateBund * 100).toFixed(2)}%`}</Typography>
                        </Box>
                    </Box>
                </Box>

                <Box sx={{ mb: 3 }}>
                    <Typography variant="subtitle2" gutterBottom sx={{ color: 'primary.main' }}>Cantonal and Municipal Tax</Typography>
                    <Typography variant="caption" color="text.secondary" sx={{ mb: 1 }}>
                        Cantonal and municipal taxes are calculated based on your cantonal taxable income and wealth. 
                        The rates vary by canton and municipality.
                    </Typography>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2">Income Tax</Typography>
                        <Box sx={{ textAlign: 'right' }}>
                            <Typography variant="body2">{(getStateIncome() * user?.tarif.taxRateIncome).toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                            <Typography variant="caption" color="text.secondary">{`${getStateIncome().toLocaleString()}  ${(user?.tarif.taxRateIncome * 100).toFixed(2)}%`}</Typography>
                        </Box>
                    </Box>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2">Wealth Tax</Typography>
                        <Box sx={{ textAlign: 'right' }}>
                            <Typography variant="body2">{(getWealth() * user?.tarif.taxRateWealth).toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                            <Typography variant="caption" color="text.secondary">{`${getWealth().toLocaleString()}  ${(user?.tarif.taxRateWealth * 100).toFixed(2)}%`}</Typography>
                        </Box>
                    </Box>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2">Cantonal Tax</Typography>
                        <Box sx={{ textAlign: 'right' }}>
                            <Typography variant="body2">{getKantonTax().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                            <Typography variant="caption" color="text.secondary">{`${getSimpleTax().toLocaleString()}  ${(user?.tarif.taxRateKanton * 100).toFixed(2)}%`}</Typography>
                        </Box>
                    </Box>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2">Municipal Tax</Typography>
                        <Box sx={{ textAlign: 'right' }}>
                            <Typography variant="body2">{getGemeindeTax().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                            <Typography variant="caption" color="text.secondary">{`${getSimpleTax().toLocaleString()}  ${(user?.tarif.taxRateGemeinde * 100).toFixed(2)}%`}</Typography>
                        </Box>
                    </Box>
                </Box>

                <Divider sx={{ my: 2 }} />

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1, fontWeight: 'bold' }}>
                    <Typography variant="body2">Total Tax</Typography>
                    <Box sx={{ textAlign: 'right' }}>
                        <Typography variant="body2">{getTotalTax().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                    </Box>
                </Box>
                <Typography variant="caption" color="text.secondary">
                    (Federal Tax + Cantonal Tax + Municipal Tax)
                </Typography>
            </Box>
        )
    }

    return (
        <Card sx={{ bgcolor: 'background.paper' }}>
            <Typography variant="h6" sx={{ mb: 2 }}>Overview</Typography>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={6} md={3}>
                    <Box sx={{ p: 2, bgcolor: 'background.default', borderRadius: 2, height: '100%' }}>
                        <Typography variant="body2" color="text.secondary">Taxable Income (Federal)</Typography>
                        <Typography variant="h6">{getFederalIncome().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                    </Box>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <Box sx={{ p: 2, bgcolor: 'background.default', borderRadius: 2, height: '100%' }}>
                        <Typography variant="body2" color="text.secondary">Taxable Income (State)</Typography>
                        <Typography variant="h6">{getStateIncome().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                    </Box>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <Box sx={{ p: 2, bgcolor: 'background.default', borderRadius: 2, height: '100%' }}>
                        <Typography variant="body2" color="text.secondary">Taxable Wealth</Typography>
                        <Typography variant="h6">{getWealth().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                    </Box>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <Box sx={{ p: 2, bgcolor: 'primary.light', borderRadius: 2, height: '100%' }}>
                        <Typography variant="body2" color="primary.contrastText">Total Tax Due</Typography>
                        <Typography variant="h6" color="primary.contrastText">{getTotalTax().toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
                    </Box>
                </Grid>
            </Grid>

            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
                <Button
                    size="small"
                    endIcon={showExtendedView ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                    onClick={() => setShowExtendedView(!showExtendedView)}
                    sx={{ textTransform: 'none' }}
                >
                    {showExtendedView ? 'Hide' : 'Show'} Calculation Details
                </Button>
            </Box>

            <Collapse in={showExtendedView} timeout="auto" unmountOnExit>
                <Box sx={{ mt: 2, p: 2, bgcolor: 'background.default', borderRadius: 1 }}>
                    {user?.tarif ? taxesBreakdown() : ''}
                </Box>
            </Collapse>
        </Card>
    );
};