import CloseIcon from '@mui/icons-material/Close';
import {
  Box,
  Button,
  CircularProgress,
  createTheme,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  Snackbar,
  ThemeProvider,
  Typography,
  useMediaQuery
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Navigate, Route, BrowserRouter as Router, Routes, useLocation } from 'react-router-dom';
import { Navigation } from './components/Navigation';
import { fetchUser } from './data';
import { User } from './model';
import { Assistant } from './pages/Assistant';
import { Auth } from './pages/Auth';
import { Home } from './pages/Home';
import { Intro } from './pages/Intro';
import { Relations } from './pages/Relations';
import { Taxes } from './pages/Taxes/Taxes';
import { Wealth } from './pages/Wealth';


const theme = createTheme({
  palette: {
    primary: {
      main: '#FF0402',
    },
    secondary: {
      main: '#FF8C00', // Changed to a warm orange that complements the primary red
    },
    background: {
      default: '#F9FAFB',
      paper: '#FFFFFF',
    },
    text: {
      primary: '#111827',
      secondary: '#6B7280',
    },
  },
  typography: {
    fontFamily: 'SF Pro Display, Arial, sans-serif',
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
          border: '1px solid #E5E7EB',
          borderRadius: 4,
          padding: 16,
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 4,
          textTransform: 'none',
          fontWeight: 600,
          boxShadow: 'none',
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            borderRadius: 4,
          },
        },
      },
    },
    MuiAvatar: {
      styleOverrides: {
        root: {
          borderRadius: 4,
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
        },
      },
    },
  },
});

const AppContent: React.FC = () => {
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));
  const location = useLocation();
  const [user, setUser] = useState<User | undefined>(undefined);
  const [deferredPrompt, setDeferredPrompt] = useState<any>(null);
  const [showInstallPrompt, setShowInstallPrompt] = useState(false);
  const [isIOS, setIsIOS] = useState(false);
  const [isInStandaloneMode, setIsInStandaloneMode] = useState(false);
  const [showInstallInstructions, setShowInstallInstructions] = useState(false);
  const [showInstallBanner, setShowInstallBanner] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  //const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const ios = /iphone|ipad|ipod/.test(window.navigator.userAgent.toLowerCase());
    setIsIOS(ios);

    const standalone = (window.navigator as any).standalone === true;
    setIsInStandaloneMode(standalone);

    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault();
      setDeferredPrompt(e);
      setShowInstallPrompt(true);
    };

    if (!ios) {
      window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    }

    return () => {
      if (!ios) {
        window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      }
    };
  }, []);

  const handleInstallClick = () => {
    if (deferredPrompt) {
      deferredPrompt.prompt();
      deferredPrompt.userChoice.then((choiceResult: { outcome: string }) => {
        if (choiceResult.outcome === 'accepted') {
          console.log('User accepted the install prompt');
        } else {
          console.log('User dismissed the install prompt');
        }
        setDeferredPrompt(null);
        setShowInstallPrompt(false);
      });
    }
  };

  const handleShowInstallInstructions = () => {
    setShowInstallInstructions(true);
    setShowInstallPrompt(false);
  };

  const handleCloseBanner = () => {
    setShowInstallBanner(false);
  };

  useEffect(() => {
    console.log(`Route changed to: ${location.pathname}`);
  }, [location]);

  useEffect(() => {
    console.log("Fetching data")
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const user: User = await fetchUser()

        setUser(user)
      } catch (error) {
        console.error(error)
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  if (isLoading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  /*
  if (!isAuthenticated && location.pathname !== '/auth') {
    return <Navigate to="/auth" replace />;
  }*/

  return (
    <Box sx={{ display: 'flex', bgcolor: 'background.default', minHeight: '100vh' }}>
      {isDesktop && location.pathname !== '/' && location.pathname !== '/auth' && <Navigation />}
      <Box sx={{
        flexGrow: 1,
        pb: { xs: 7, md: 0 } // Add padding bottom on mobile
      }}>
        <Routes>
          <Route path="/" element={<Intro />} />
          <Route path="/auth" element={<Auth />} />
          <Route path="/home" element={<Home user={user} />} />
          <Route path="/assistant" element={<Assistant user={user} />} />
          <Route path="/relations" element={<Relations user={user} />} />
          <Route path="/wealth" element={<Wealth user={user} />} />
          <Route path="/taxes" element={<Taxes user={user} />} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Box>
      {!isDesktop && location.pathname !== '/' && location.pathname !== '/auth' && <Navigation />}
      <Snackbar
        open={(showInstallPrompt || (isIOS && !isInStandaloneMode)) && showInstallBanner}
        message={isIOS ? "Add this app to your home screen" : "Install this app on your home screen"}
        action={
          <>
            <Button
              color="secondary"
              size="small"
              onClick={isIOS ? handleShowInstallInstructions : handleInstallClick}
            >
              {isIOS ? "How to install" : "Install"}
            </Button>
            <IconButton
              size="small"
              aria-label="close"
              color="inherit"
              onClick={handleCloseBanner}
            >
              <CloseIcon fontSize="small" />
            </IconButton>
          </>
        }
      />
      <Dialog open={showInstallInstructions} onClose={() => setShowInstallInstructions(false)}>
        <DialogTitle>How to Install on iOS</DialogTitle>
        <DialogContent>
          <Typography variant="body1">
            1. Tap the Share icon in Safari's toolbar.<br />
            2. Scroll down and tap "Add to Home Screen".<br />
            3. Enter a name for the app and tap "Add".
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowInstallInstructions(false)}>Close</Button>
        </DialogActions>
      </Dialog>
      {isIOS && !isInStandaloneMode && showInstallBanner && (
        <Box sx={{ position: 'fixed', bottom: 0, left: 0, right: 0, p: 2, bgcolor: 'background.paper', zIndex: 1000 }}>
          <Typography variant="body2" align="center">
            To install, tap the share icon and then "Add to Home Screen"
          </Typography>
          <IconButton
            size="small"
            aria-label="close"
            onClick={handleCloseBanner}
            sx={{ position: 'absolute', right: 8, top: 8 }}
          >
            <CloseIcon fontSize="small" />
          </IconButton>
        </Box>
      )}
    </Box>
  );
};

export const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <AppContent />
      </Router>
    </ThemeProvider>
  );
};