import { Box, Card, Table, TableBody, TableCell, TableContainer, TableRow, Typography } from '@mui/material';
import React from 'react';
import { User } from "../../model";
import { areAllRequiredAttributesDefined, taxDeductibles } from "../../model-deductibles";

export const TaxesDeductibles = ({ user }: { user: User }) => {
  const sortedDeductibles = taxDeductibles.map(deductible => {
    const canCalculate = areAllRequiredAttributesDefined(user.details, deductible);
    const isApplicable = deductible.isApplicable(user.details);
    let stateValue = 0;
    let federalValue = 0;

    if (canCalculate && isApplicable) {
      stateValue = deductible.calculateState ? deductible.calculateState(user.details) : 0;
      federalValue = deductible.calculateFederal ? deductible.calculateFederal(user.details) : 0;
    }

    return {
      deductible,
      canCalculate,
      isApplicable,
      stateValue,
      federalValue,
    };
  });

  // Group deductibles by category
  const groupedDeductibles = sortedDeductibles.reduce((acc, { deductible, canCalculate, isApplicable, stateValue, federalValue }) => {
    const category = deductible.category;
    if (!acc[category]) {
      acc[category] = [];
    }
    acc[category].push({ deductible, canCalculate, isApplicable, stateValue, federalValue });
    return acc;
  }, {} as Record<string, any[]>);

  const totalStateDeduction = sortedDeductibles.reduce((sum, { stateValue }) => sum + stateValue, 0);
  const totalFederalDeduction = sortedDeductibles.reduce((sum, { federalValue }) => sum + federalValue, 0);

  return (
    <Card sx={{ height: '100%', bgcolor: 'background.paper', p: 0 }}>
      <Typography variant="h6" sx={{ p: 2, borderBottom: '1px solid rgba(224, 224, 224, 1)' }}>Deductibles</Typography>
      <TableContainer>
        <Table size="small">
          <TableBody>
            {Object.entries(groupedDeductibles).map(([category, deductibles]) => (
              <React.Fragment key={category}>
                <TableRow>
                  <TableCell colSpan={2} sx={{ bgcolor: 'action.hover' }}>
                    <Typography variant="subtitle1">{category}</Typography>
                  </TableCell>
                </TableRow>
                {deductibles.map(({ deductible, canCalculate, isApplicable, stateValue, federalValue }) => (
                  <TableRow key={deductible.name} sx={{ '&:last-child td, &:last-child th': { border: 0 }, p: 0 }}>
                    <TableCell component="th" scope="row" sx={{ color: 'text.secondary', p: 2 }}>
                      <Typography variant="body2" sx={{ fontWeight: 'medium' }}>{deductible.name}</Typography>
                      <Typography variant="caption" color="text.secondary">{deductible.description}</Typography>
                      {!canCalculate ? (
                        <Typography variant="caption" color="error.main" display="block" sx={{ mt: 2 }}>
                          Insufficient data to calculate deduction
                        </Typography>
                      ) : !isApplicable && (
                        <Typography variant="caption" color="error.main" display="block" sx={{ mt: 2 }}>
                          This deduction is not applicable
                        </Typography>
                      )}
                    </TableCell>
                    <TableCell align="right">
                      <Box sx={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                        <Box sx={{ textAlign: 'right', width: '50%', pr: 2 }}>
                          <Typography variant="body2">
                            {stateValue.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                          </Typography>
                        </Box>
                        <Box sx={{ textAlign: 'right', width: '50%' }}>
                          <Typography variant="body2">
                            {federalValue.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                          </Typography>
                        </Box>
                      </Box>
                    </TableCell>
                  </TableRow>
                ))}
              </React.Fragment>
            ))}
            <TableRow>
              <TableCell component="th" scope="row" sx={{ fontWeight: 'bold', borderTop: '1px solid rgba(224, 224, 224, 1)', p : 2 }}>
                Total
              </TableCell>
              <TableCell align="right" sx={{ fontWeight: 'bold', borderTop: '1px solid rgba(224, 224, 224, 1)' }}>
                <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                  <Box sx={{ width: '50%', textAlign: 'right', pr: 2 }}>{totalStateDeduction.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}</Box>
                  <Box sx={{ width: '50%', textAlign: 'right' }}>{totalFederalDeduction.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}</Box>
                </Box>
              </TableCell>
            </TableRow>

          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  );
};