import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import AgricultureIcon from '@mui/icons-material/Agriculture';
import BusinessIcon from '@mui/icons-material/Business';
import EmailIcon from '@mui/icons-material/Email';
import LanguageIcon from '@mui/icons-material/Language';
import PersonIcon from '@mui/icons-material/Person';
import PhoneIcon from '@mui/icons-material/Phone';
import SearchIcon from '@mui/icons-material/Search';
import SecurityIcon from '@mui/icons-material/Security';
import SportsIcon from '@mui/icons-material/Sports';
import WorkIcon from '@mui/icons-material/Work';

import {
  Avatar,
  Box,
  Button,
  Card,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  InputAdornment,
  MenuItem,
  Select,
  TextField,
  Tooltip,
  Typography
} from '@mui/material';
import { useState } from 'react';
import { User } from '../model';

const getCategoryIcon = (category: string) => {
  switch (category.toLowerCase()) {
    case 'financial services':
    case 'banking':
      return <AccountBalanceIcon />;
    case 'insurance':
      return <SecurityIcon />;
    case 'employer':
    case 'consulting':
      return <WorkIcon />;
    case 'sports':
      return <SportsIcon />;
    case 'agriculture':
      return <AgricultureIcon />;
    case 'person':
      return <PersonIcon />;
    default:
      return <BusinessIcon />;
  }
};

const getCategoryColor = (category: string) => {
  return "#FF0402";
};

const truncateName = (name: string, maxLength: number = 20) => {
  if (name.length <= maxLength) return name;
  return `${name.substring(0, maxLength - 3)}...`;
};

export const Relations = ({ user }: { user: User }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [contacts] = useState(user?.contacts.map(contact => ({
    ...contact,
    phone: '+41 123 456 789',
    email: `info@${contact.fullName.toLowerCase().replace(/\s/g, '')}.com`,
    website: `www.${contact.fullName.toLowerCase().replace(/\s/g, '')}.com`,
  })));
  const [editingContact, setEditingContact] = useState<null | (typeof contacts)[0]>(null);
  const [duplicateContact, setDuplicateContact] = useState<null | (typeof contacts)[0]>(null);
  const [selectedDuplicateOf, setSelectedDuplicateOf] = useState('');

  const filteredContacts = contacts?.filter(contact =>
    contact.fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    contact.category.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Group contacts by category
  const groupedContacts = filteredContacts?.reduce((acc, contact) => {
    const category = contact.category.charAt(0).toUpperCase() + contact.category.slice(1);
    if (!acc[category]) {
      acc[category] = [];
    }
    acc[category].push(contact);
    return acc;
  }, {} as Record<string, typeof filteredContacts>);

  const handleEditContact = (contact: (typeof contacts)[0]) => {
    setEditingContact(contact);
  };

  const handleSaveContact = () => {
    if (editingContact) {
      /*
      setContacts(prevContacts =>
        prevContacts.map(c => c.id === editingContact.id ? editingContact : c)
      );*/
      setEditingContact(null);
    }
  };

  const handleMarkAsDuplicate = (contact: (typeof contacts)[0]) => {
    setDuplicateContact(contact);
  };

  const handleConfirmDuplicate = () => {
    // Implement the logic to mark the contact as a duplicate
    setDuplicateContact(null);
    setSelectedDuplicateOf('');
  };

  return (
    <Box sx={{ p: 2, bgcolor: 'background.default' }}>
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}><Box component="span" sx={{ color: 'primary.main' }}>You</Box> Relations</Typography>
      <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">
        Explore them
      </Typography>

      <TextField
        fullWidth
        variant="outlined"
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchIcon color="action" />
            </InputAdornment>
          ),
        }}
        sx={{
          mb: 2,
          bgcolor: 'background.paper',
          '& .MuiOutlinedInput-root': {
            '& fieldset': { borderColor: '#E5E7EB' },
            '&:hover fieldset': { borderColor: '#E5E7EB' },
            '&.Mui-focused fieldset': { borderColor: 'primary.main' },
          },
        }}
      />

      {groupedContacts && Object.entries(groupedContacts).map(([category, contacts]) => (
        <Box key={category} sx={{ mb: 4 }}>
          <Typography variant="h5" sx={{ mb: 2, color: "text.secondary" }}>
            {category}
          </Typography>
          <Grid container spacing={2}>
            {contacts.map((contact, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Card sx={{ p: 2, height: '100%' }}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <Avatar sx={{ bgcolor: getCategoryColor(contact.category), color: 'white', mr: 2 }}>
                      {getCategoryIcon(contact.category)}
                    </Avatar>
                    <Box>
                      <Tooltip title={contact.fullName}>
                        <Typography variant="subtitle1" fontWeight="medium">
                          {truncateName(contact.fullName)}
                        </Typography>
                      </Tooltip>
                      <Typography variant="body2" color="text.secondary">{contact.category.charAt(0).toUpperCase() + contact.category.slice(1)}</Typography>
                    </Box>
                  </Box>
                  <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <PhoneIcon fontSize="small" sx={{ mr: 1 }} />
                      <Typography variant="body2">{contact.phone}</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <EmailIcon fontSize="small" sx={{ mr: 1 }} />
                      <Typography variant="body2">test@test.com</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <LanguageIcon fontSize="small" sx={{ mr: 1 }} />
                      <Typography variant="body2">test.com</Typography>
                    </Box>
                  </Box>
                  <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
                    <Button onClick={() => handleEditContact(contact)}>Edit</Button>
                    <Button onClick={() => handleMarkAsDuplicate(contact)}>Mark as Duplicate</Button>
                  </Box>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      ))}

      <Dialog open={!!editingContact} onClose={() => setEditingContact(null)}>
        <DialogTitle>Edit Contact</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Full Name"
            fullWidth
            value={editingContact?.fullName || ''}
            onChange={(e) => setEditingContact(prev => prev ? {...prev, fullName: e.target.value} : null)}
          />
          <Select
            fullWidth
            value={editingContact?.category || ''}
            onChange={(e) => setEditingContact(prev => prev ? {...prev, category: e.target.value} : null)}
            sx={{ mt: 2 }}
          >
            <MenuItem value="Financial Services">Financial Services</MenuItem>
            <MenuItem value="Banking">Banking</MenuItem>
            <MenuItem value="Insurance">Insurance</MenuItem>
            <MenuItem value="Employer">Employer</MenuItem>
            <MenuItem value="Consulting">Consulting</MenuItem>
            <MenuItem value="Sports">Sports</MenuItem>
            <MenuItem value="Agriculture">Agriculture</MenuItem>
            <MenuItem value="Person">Person</MenuItem>
          </Select>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setEditingContact(null)}>Cancel</Button>
          <Button onClick={handleSaveContact}>Save</Button>
        </DialogActions>
      </Dialog>

      <Dialog open={!!duplicateContact} onClose={() => setDuplicateContact(null)}>
        <DialogTitle>Mark as Duplicate</DialogTitle>
        <DialogContent>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Select the contact that this is a duplicate of:
          </Typography>
          <Select
            fullWidth
            value={selectedDuplicateOf || ''}
            onChange={(e) => setSelectedDuplicateOf(e.target.value)}
          >
            {contacts.filter(c => c.fullName !== duplicateContact?.fullName).map((contact) => (
              <MenuItem key={contact.fullName} value={contact.fullName}>{contact.fullName}</MenuItem>
            ))}
          </Select>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
            setDuplicateContact(null);
            setSelectedDuplicateOf('');
          }}>Cancel</Button>
          <Button onClick={handleConfirmDuplicate} disabled={!selectedDuplicateOf}>Confirm</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};