import SearchIcon from '@mui/icons-material/Search';
import {
  Box,
  Card,
  Collapse,
  Grid,
  InputAdornment,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  TextField,
  Typography
} from '@mui/material';
import React, { useState } from 'react';
import { Taxable, TaxableSubType, TaxableType, User } from '../../model';
import { TaxableSubTypeDescriptions } from '../descriptions';
import { TaxesDeductibles } from './TaxesDeductibles';
import { TaxesOverview } from './TaxesOverview';


export const Taxes = ({ user }: { user: User }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedYear, setSelectedYear] = useState(2022);

  const years = [2022, 2021, 2020]; // Add more years as needed

  const handleYearChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedYear(Number(event.target.value));
  };

  const getTaxables = () => {
    return user?.taxables?.filter(t => t.taxYear === selectedYear) || [];
  }

  const filterTaxable = (type: TaxableType) => {
    return getTaxables().filter(t => t.type === type);
  }

  const filteredTaxables = getTaxables().filter(t =>
    TaxableSubTypeDescriptions[t.subType].toLowerCase().includes(searchTerm.toLowerCase()) ||
    t.other?.fullName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <Box sx={{ p: 2, bgcolor: 'background.default' }}>
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}><Box component="span" sx={{ color: 'primary.main' }}>You</Box> Taxes</Typography>
      <Box sx={{ mb: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Typography variant="h5" color="text.secondary">Fiscal Year</Typography>
        <TextField
          select
          value={selectedYear}
          onChange={handleYearChange}
          variant="outlined"
          size="small"
          sx={{
            minWidth: 120,
            '& .MuiOutlinedInput-root': {
              '& fieldset': { borderColor: 'primary.main' },
              '&:hover fieldset': { borderColor: 'primary.main' },
              '&.Mui-focused fieldset': { borderColor: 'primary.main' },
            },
            '& .MuiSelect-select': {
              paddingY: 1,
              color: 'primary.main',
              fontWeight: 'bold',
            },
          }}
        >
          {years.map((year) => (
            <MenuItem key={year} value={year}>
              {year}
            </MenuItem>
          ))}
        </TextField>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TaxesOverview user={user} taxables={getTaxables()} />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            variant="outlined"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon color="action" />
                </InputAdornment>
              ),
            }}
            sx={{
              bgcolor: 'background.paper',
              '& .MuiOutlinedInput-root': {
                '& fieldset': { borderColor: '#E5E7EB' },
                '&:hover fieldset': { borderColor: '#E5E7EB' },
                '&.Mui-focused fieldset': { borderColor: 'primary.main' },
              },
            }}
          />
        </Grid>
        {searchTerm ? (
          <Grid item xs={12}>
            <TaxSection
              title="Search Results"
              taxables={filteredTaxables}
              isSearchResult={true}
            />
          </Grid>
        ) : (
          <>
            <Grid item xs={12}>
              <TaxSection
                title="Income"
                taxables={filterTaxable(TaxableType.INCOME)}
              />
            </Grid>

            <Grid item xs={12}>
              <TaxSection
                title="Wealth"
                taxables={filterTaxable(TaxableType.ASSET)}
              />
            </Grid>

            <Grid item xs={12}>
              <TaxesDeductibles user={user} />
            </Grid>
          </>
        )}
      </Grid>
    </Box>
  );
};

const TaxSection = ({ title, taxables, isSearchResult = false }: { title: string, taxables: Taxable[], isSearchResult?: boolean }) => {
  const groupTaxablesBySubTypeAndSum = (taxables: Taxable[]): { subType: TaxableSubType; totalAmountNet: number, taxables: Taxable[] }[] => {
    const groupedTaxables = taxables.reduce((acc, t) => {
      acc[t.subType] = (acc[t.subType] || 0) + t.amount;
      return acc;
    }, {} as { [key in TaxableSubType]?: number });
  
    return Object.entries(groupedTaxables)
      .map(([subType, totalAmountNet]) => ({
        subType: subType as TaxableSubType,
        totalAmountNet: totalAmountNet || 0,
        taxables: taxables.filter(t => t.subType === subType)
      }))
      .sort((a: any, b: any) => b.totalAmountNet - a.totalAmountNet);
  }
  
  const [expandedItems, setExpandedItems] = useState<Set<TaxableSubType>>(new Set());
  const groupedTaxables = isSearchResult ? [] : groupTaxablesBySubTypeAndSum(taxables);
  const total = taxables.reduce((sum, item) => sum + item.amount, 0);

  const handleExpand = (subType: TaxableSubType) => {
    setExpandedItems(prevItems => {
      const newItems = new Set(prevItems);
      if (newItems.has(subType)) {
        newItems.delete(subType);
      } else {
        newItems.add(subType);
      }
      return newItems;
    });
  };

  return (
    <Card sx={{ height: '100%', bgcolor: 'background.paper', p: 0 }}>
      <Typography variant="h6" sx={{ display: 'flex', justifyContent: 'space-between', p: 2 }}>
        {title}
      </Typography>
      <TableContainer>
        <Table size="small">
          <TableBody>
            {isSearchResult ? (
              taxables.map((taxable, index) => (
                <TableRow key={index} hover>
                  <TableCell component="th" scope="row" sx={{ color: 'text.secondary' }}>
                    {TaxableSubTypeDescriptions[taxable.subType]} - {taxable.other?.fullName}
                  </TableCell>
                  <TableCell align="right">
                    {taxable.amount.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                  </TableCell>
                </TableRow>
              ))
            ) : (
              groupedTaxables.map((item, index) => (
                <React.Fragment key={item.subType}>
                  <TableRow
                    sx={{
                      '&:last-child td, &:last-child th': { border: 0 },
                      cursor: 'pointer',
                      '&:hover': { backgroundColor: 'action.hover' },
                      // Hide bottom border if next item is not expanded
                      '& td, & th': {
                        borderBottom: expandedItems.has(item.subType) ? '1px solid rgba(224, 224, 224, 1)' : 'none',
                      },
                    }}
                    onClick={() => handleExpand(item.subType)}
                  >
                    <TableCell component="th" scope="row" sx={{ color: 'text.secondary', display: 'flex', alignItems: 'center', p: 2 }}>
                      <Typography variant="body2">{TaxableSubTypeDescriptions[item.subType]}</Typography>
                    </TableCell>
                    <TableCell align="right">
                      <Typography variant="body2">
                        {item.totalAmountNet.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                      </Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell colSpan={2} sx={{ py: 0, borderBottom: 'none' }}>
                      <Collapse in={expandedItems.has(item.subType)} timeout="auto" unmountOnExit>
                        <Box sx={{ my: 2 }}>
                          <TaxableDetails subType={item.subType} taxables={taxables} />
                        </Box>
                      </Collapse>
                    </TableCell>
                  </TableRow>
                </React.Fragment>
              ))
            )}
            <TableRow>
              <TableCell component="th" scope="row" sx={{ fontWeight: 'bold', borderTop: '1px solid rgba(224, 224, 224, 1)', p: 2 }}>
                Total
              </TableCell>
              <TableCell align="right" sx={{ fontWeight: 'bold', borderTop: '1px solid rgba(224, 224, 224, 1)' }}>
                {total.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  );
};

const TaxableDetails = ({ subType, taxables }: { subType: TaxableSubType, taxables: Taxable[] }) => {
  const filteredTaxables = taxables.filter(t => t.subType === subType);

  return (
    <Table size="small">
      <TableBody>
        {filteredTaxables.sort((a: Taxable, b: Taxable) => a.other?.fullName.localeCompare(b.other?.fullName)).map((taxable, index) => (
          <TableRow key={index} hover>
            <TableCell sx={{ borderBottom: 'none', pl: 0, pr: 0 }}>
              <Typography variant="body2">{taxable.self?.fullName} -  {taxable.other?.fullName}</Typography>
            </TableCell>
            <TableCell align="right" sx={{ borderBottom: 'none', pl: 0, pr: 0 }}>
              <Typography variant="body2">
                {taxable.amount.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
              </Typography>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};