import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import SearchIcon from '@mui/icons-material/Search';
import ShowChartIcon from '@mui/icons-material/ShowChart';

import {
  Avatar,
  Box,
  Card,
  Collapse,
  Divider,
  Grid,
  IconButton,
  InputAdornment,
  List,
  ListItem,
  TextField,
  Typography
} from '@mui/material';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { AssetType, Taxable, TaxableType, User } from '../model';
import { TaxableSubTypeDescriptions } from './descriptions';

export const Wealth = ({ user }: { user: User }) => {
  const [expandedAssets, setExpandedAssets] = useState<Set<string>>(new Set());
  const [searchTerm, setSearchTerm] = useState('');

  const groupAssetsByTypeAndSum = useCallback(() => {
    const groupedAssets = user?.taxables.filter(t => t.type === TaxableType.ASSET)
      .reduce((acc, a) => {
        acc[a.subType] = (acc[a.subType] || 0) + a.amount;
        return acc;
      }, {} as { [key in AssetType]?: number }) || [];

    return Object.entries(groupedAssets)
      .map(([type, totalAmountNet]) => ({
        subType: type as AssetType,
        value: totalAmountNet
      }))
      .sort((a: any, b: any) => b.value - a.value);
  }, [user?.taxables]);

  const getIconForType = useCallback((subType: AssetType) => {
    switch (subType) {
      case AssetType.CASH:
        return <AccountBalanceIcon />;
      case AssetType.PORTFOLIO:
      case AssetType.ASSET:
      case AssetType.CRYPTO:
      case AssetType.PRIVATE_EQUITY:
        return <ShowChartIcon />;
      default:
        return <AccountBalanceIcon />;
    }
  }, []);

  const getColorForType = useCallback((subType: AssetType) => {
    switch (subType) {
      case AssetType.CASH:
        return '#FF0402'; // Primary color
      case AssetType.PORTFOLIO:
        return '#66BB6A'; // Light green
      case AssetType.ASSET:
        return '#4CAF50'; // Medium green
      case AssetType.CRYPTO:
        return '#43A047'; // Dark green
      case AssetType.PRIVATE_EQUITY:
        return '#2E7D32'; // Darker green
      default:
        return '#C8E6C9'; // Very light green for any other cases
    }
  }, []);

  const assetData = useMemo(() => groupAssetsByTypeAndSum()
    .map(item => ({
      subType: item.subType,
      value: item.value,
      icon: getIconForType(item.subType),
      color: getColorForType(item.subType),
    })), [groupAssetsByTypeAndSum, getIconForType, getColorForType]);
  console.log(assetData)

  const totalAssets = useMemo(() => assetData.reduce((sum, asset) => sum + asset.value, 0), [assetData]);

  useEffect(() => {
    if (searchTerm) {
      const matchingGroups = new Set<string>();
      const matchingAssets = new Set<string>();

      user?.taxables
        .filter(t => t.type === TaxableType.ASSET)
        .forEach(a => {
          if (a.other?.fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            (a.reference?.references.length > 0 && a.reference.references[0].value.toLowerCase().includes(searchTerm.toLowerCase()))) {
            matchingGroups.add(a.other.fullName);
            matchingAssets.add(a.subType);
          }
        });

      setExpandedAssets(matchingAssets);
    } else {
      setExpandedAssets(new Set());
    }
  }, [searchTerm, user?.taxables]);

  const renderAssetBar = () => (
    <Box sx={{ mb: 3, display: 'flex', height: 10, borderRadius: 2, overflow: 'hidden' }}>
      {assetData.map((asset, index) => (
        <Box
          key={index}
          sx={{
            height: '100%',
            bgcolor: asset.color,
            width: `${(asset.value / totalAssets) * 100}%`,
            transition: 'width 0.3s ease-in-out',
          }}
        />
      ))}
    </Box>
  );

  const renderAssetCard = (asset: typeof assetData[0], index: number) => {
    const subAssets = user?.taxables.filter(a => a.subType === asset.subType) || [];
    const groupedByOther = groupSubAssetsByOther(subAssets);
    const filteredGroups = filterGroupsBySearchTerm(groupedByOther, searchTerm);

    if (filteredGroups.length === 0) return null;

    return (
      <Card key={index} sx={{ mb: 3, bgcolor: 'background.paper', overflow: 'hidden', p: 0 }}>
        <AssetCardHeader asset={asset} totalAssets={totalAssets} expandedAssets={expandedAssets} setExpandedAssets={setExpandedAssets} />
        <Collapse in={expandedAssets.has(asset.subType) || searchTerm !== ''}>
          <Divider />
          <AssetCardContent asset={asset} totalAssets={totalAssets} filteredGroups={filteredGroups} />
        </Collapse>
      </Card>
    );
  };

  return (
    <Box sx={{ p: 2, bgcolor: 'background.default' }}>
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}>
        <Box component="span" sx={{ color: 'primary.main' }}>You</Box> Wealth
      </Typography>
      <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">
        {totalAssets.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
      </Typography>
      <SearchField searchTerm={searchTerm} setSearchTerm={setSearchTerm} />
      {renderAssetBar()}
      {assetData.map(renderAssetCard)}
    </Box>
  );
};

// New components (to be defined outside of the main Wealth component):
const SearchField = ({ searchTerm, setSearchTerm }) => {
  return (
    <TextField
      fullWidth
      variant="outlined"
      value={searchTerm}
      onChange={(e) => setSearchTerm(e.target.value)}
      InputProps={{
        startAdornment: (
          <InputAdornment position="start">
            <SearchIcon color="action" />
          </InputAdornment>
        ),
      }}
      sx={{
        mb: 3,
        bgcolor: 'background.paper',
        '& .MuiOutlinedInput-root': {
          '& fieldset': { borderColor: '#E5E7EB' },
          '&:hover fieldset': { borderColor: '#E5E7EB' },
          '&.Mui-focused fieldset': { borderColor: 'primary.main' },
        },
      }}
    />
  );
};

const AssetCardHeader = ({ asset, totalAssets, expandedAssets, setExpandedAssets }) => {
  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        p: 2,
        cursor: 'pointer',
        '&:hover': { bgcolor: 'action.hover' },
      }}
      onClick={() => {
        const newExpandedAssets = new Set(expandedAssets);
        if (newExpandedAssets.has(asset.subType)) {
          newExpandedAssets.delete(asset.subType);
        } else {
          newExpandedAssets.add(asset.subType);
        }
        setExpandedAssets(newExpandedAssets);
      }}
    >
      <Avatar sx={{ bgcolor: asset.color, color: 'white', mr: 2 }}>{asset.icon}</Avatar>
      <Box sx={{ flexGrow: 1 }}>
        <Typography variant="subtitle1" fontWeight="medium">{TaxableSubTypeDescriptions[asset.subType]}</Typography>
        <Typography variant="body2" color="text.secondary">
          {((asset.value / totalAssets) * 100).toFixed(1)}% of total
        </Typography>
      </Box>
      <Typography variant="subtitle1" fontWeight="bold" sx={{ mr: 2 }}>
        {asset.value.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
      </Typography>
      <IconButton size="small">
        {expandedAssets.has(asset.subType) ? <ExpandLessIcon /> : <ExpandMoreIcon />}
      </IconButton>
    </Box>
  );
};

const AssetCardContent = ({ asset, totalAssets, filteredGroups }) => {
  return (
    <Box sx={{ p: 2 }}>
      <Box sx={{
        height: 6,
        bgcolor: 'action.hover',
        borderRadius: 3,
        overflow: 'hidden',
        mb: 2
      }}>
        <Box sx={{
          width: `${(asset.value / totalAssets) * 100}%`,
          height: '100%',
          bgcolor: asset.color,
          transition: 'width 0.3s ease-in-out',
        }} />
      </Box>
      {filteredGroups.map(([providerName, subAssets]) => (
        <Box key={providerName} sx={{ mb: 3, bgcolor: 'background.paper', borderRadius: 1, boxShadow: 1, overflow: 'hidden' }}>
          <Typography variant="subtitle1" sx={{ p: 2, fontWeight: 'bold', bgcolor: 'action.hover' }}>
            {providerName}
          </Typography>
          <List dense disablePadding>
            {subAssets.map((subAsset, subAssetIndex) => (
              <ListItem
                key={`${providerName}-${subAssetIndex}`}
                sx={{
                  py: 1.5,
                  px: 2,
                  borderBottom: '1px solid rgba(0, 0, 0, 0.08)',
                  '&:hover': { bgcolor: 'action.hover' },
                  '&:last-child': { borderBottom: 'none' },
                }}
              >
                <Grid container alignItems="center" spacing={2}>
                  <Grid item xs={8}>
                    <Typography variant="body2" sx={{ fontWeight: 'medium', color: 'text.primary' }}>
                      {subAsset.reference?.references.length > 0 ? subAsset.reference?.references[0].value : 'N/A'}
                    </Typography>
                    <Typography variant="caption" sx={{ color: 'text.secondary' }}>
                      {subAsset.reference?.references.length > 1 ? subAsset.reference?.references[1].value : TaxableSubTypeDescriptions[subAsset.subType]}
                    </Typography>
                  </Grid>
                  <Grid item xs={4}>
                    <Typography variant="body2" align="right" fontWeight="medium" sx={{ color: 'text.primary' }}>
                      {subAsset.amount.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
                    </Typography>
                  </Grid>
                </Grid>
              </ListItem>
            ))}
          </List>
        </Box>
      ))}
    </Box>
  );
};

// Helper functions:
const groupSubAssetsByOther = (subAssets: Taxable[]) => {
  return subAssets.reduce((acc, subAsset) => {
    const key = subAsset.other?.fullName;
    if (!acc[key]) acc[key] = [];
    acc[key].push(subAsset);
    return acc;
  }, {} as { [key: string]: Taxable[] });
};

const filterGroupsBySearchTerm = (groupedByOther: { [key: string]: Taxable[] }, searchTerm: string) => {
  return Object.entries(groupedByOther).filter(([otherName, subAssets]) =>
    otherName.toLowerCase().includes(searchTerm.toLowerCase()) && subAssets.length > 0
  );
};