export enum DeductibleType {
    WORK_RELATED = 'WORK_RELATED',
    PENSION_CONTRIBUTION = 'PENSION_CONTRIBUTION',
    INSURANCE_PREMIUMS = 'INSURANCE_PREMIUMS',
    LOAN_INTEREST = 'LOAN_INTEREST',
    SOCIAL_SECURITY = 'SOCIAL_SECURITY',
    DEPENDENTS = 'DEPENDENTS',
    HEALTHCARE_INSURANCE_PREMIUM = 'HEALTHCARE_INSURANCE_PREMIUM',
    HEALTHCARE_NOT_COVERED_COST = 'HEALTHCARE_NOT_COVERED_COST',
    BANK_EXPENSES = 'BANK_EXPENSES',
    CHARITABLE_DONATIONS = 'CHARITABLE_DONATIONS',
    POLITICAL_DONATIONS = 'POLITICAL_DONATIONS',
    GIFT = 'GIFT',
    PROPERTY_MAINTENANCE = 'PROPERTY_MAINTENANCE',
    EDUCATION_TRAINING = 'EDUCATION_TRAINING',
    CHILDCARE_COST = 'CHILDCARE_COST',
    FAMILY = 'FAMILY' 
}

export type TaxableSubType = IncomeType | AssetType | DeductibleType;

export class Reference {
    objectType: string;
    references: { type: string, value: string }[] = [];

    constructor(objectType: string) {
        this.objectType = objectType;
    }

    addReference(type: string, value: string | undefined) {
        if (value && value.length > 0 && value !== "N/A") {
            this.references.push({ type: type, value: value });
        }
        return this;
    }

    same(other: Reference): boolean {
        if (!other) {
            return false;
        }
        if (this.objectType === other.objectType) {
            for (const ref of this.references) {
                for (const otherRef of other.references) {
                    if (otherRef.type === ref.type && ref.value.replace(/[\s-]/g, '') === otherRef.value.replace(/[\s-]/g, '')) {
                        return true;
                    }
                }
            }
        }
        return false;
    }
}

export class Taxable {
    type: TaxableType;

    taxYear: number | undefined;
    currency: string | undefined;

    holdings: number | undefined;

    amount: number | undefined;

    reference: Reference | undefined;

    children: Reference[] = []; // in case any positions are in the taxable (E.g. TaxStatement with reference to many positions)
    taxables: Taxable[] = [];

    self: Contact | undefined;
    other: Contact | undefined;

    subType: TaxableSubType;

    constructor(type: TaxableType, subType: TaxableSubType, self: Contact | undefined, other: Contact | undefined, taxYear: number, currency: string, amount: number | undefined) {
        this.type = type;
        this.subType = subType;
        this.self = self;
        this.other = other;
        this.taxYear = taxYear;
        this.currency = currency;
        this.amount = amount;
    }

    static Income(incomeType: IncomeType, self: Contact | undefined, other: Contact | undefined, taxYear: number, currency: string, amount: number | undefined): Taxable {
        return new Taxable(TaxableType.INCOME, incomeType, self, other, taxYear, currency, amount);
    }

    static Asset(assetType: AssetType, self: Contact | undefined, other: Contact | undefined, taxYear: number, currency: string, amount: number | undefined): Taxable {
        return new Taxable(TaxableType.ASSET, assetType, self, other, taxYear, currency, amount);
    }

    static Deductible(deductibleType: DeductibleType, self: Contact | undefined, other: Contact | undefined, taxYear: number, currency: string, amount: number | undefined): Taxable {
        return new Taxable(TaxableType.DEDUCTIBLE, deductibleType, self, other, taxYear, currency, amount);
    }
}

export enum MaritalType {
    SINGLE = 'SINGLE',
    MARRIED = 'MARRIED',
    DIVORCED = 'DIVORCED',
    WIDOWED = 'WIDOWED',
    REGISTERED_PARTNERSHIP = 'REGISTERED_PARTNERSHIP',
    SEPARATED = 'SEPARATED'
}


export class User {
    maritalState: MaritalType | undefined;
    numberOfChildren: number | undefined;

    details: UserDetails;

    taxables: Taxable[] = [];
    contacts: Contact[] = [];
    tarif: TaxTarif | undefined;
    kanton: string | undefined;
    gemeinde: string | undefined;

    findContact(contactName: string | undefined): Contact | undefined {
        if (contactName === 'Unknown') {
            return undefined;
        }
        if (!contactName || contactName.length === 0) {
            return undefined;
        }

        for (const c of this.contacts) {
            if (c.fullName.toLowerCase() === contactName.toLowerCase()) {
                return c;
            }
            for (const alternative of c.alternativeNames) {
                if (alternative.toLowerCase() === contactName.toLowerCase()) {
                    return c;
                }
            }
        }
        return new Contact(contactName);
    }

    addTaxable(taxable: Taxable) {
        this.taxables.push(taxable);
        return this;
    }

    sumTaxablesBySubType(subType: TaxableSubType) : number {
        return this.taxables
            .filter(t => t.subType === subType)
            .reduce((acc, taxable) => {
                return acc + (taxable.amount || 0);
            }, 0);
    }
}

